<template>
    <div style="margin-top: 5%;text-align: center;width: 100%" >
        <el-form :model="form"  ref="form" label-width="100px" style="margin-top: 20px;">
            <el-row style="width: 100%">
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label=""required  required style="width: 98%;" >
                            <el-input v-model="form.doctorName" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label="" required style="width: 98%;">
                            <el-input v-model="form.doctorIdCard"  placeholder=""></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label="" required style="width: 98%;">
                            <el-input v-model="form.doctorPhone"  placeholder=""></el-input>
                        </el-form-item>

                    </div>
                </el-col>
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label="" required style="width: 98%;">
                            <el-input v-model="form.verification" style="width: 72%;"  placeholder=""></el-input>
                            <el-button  :disabled="display===1?false:true" class="but" @click="getVerification()" style="font-weight: bold">{{content}}</el-button>
                        </el-form-item>
                    </div>
                </el-col>
            </el-row>

            <el-row style="width: 100%">
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label=""  style="width: 98%;" required>
                            <el-input v-model="form.doctorPassword" type="password"  placeholder="8" show-password></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label="" style="width: 98%;" required>
                            <el-input v-model="password2" type="password"  placeholder="" show-password></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label="" required>
                            <el-radio-group v-model="form.doctorSex" style="width: 150px;">
                                <el-radio label="1" value="1"></el-radio>
                                <el-radio label="2" value="2"></el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </div>
                </el-col>

                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item required label="" >
                            <el-input v-model="form.doctorAddress"  placeholder=""></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label="" prop="avator">
                            <el-upload
                                    class="avatar-uploader"
                                    action="http://124.222.128.245:955/uploadImageFile"
                                    :show-file-list="false"
                                    accept="image/jpeg,image/gif,image/png,image/jpg"
                                    :on-success="handleAvatarSuccess_avatar">
                                <img v-if="avatar" :src="avatar" class="avatar" style="width: 100px;height: 100px">
                                <i v-else class="el-icon-plus avatar-uploader-icon" slot="trigger"></i>
                            </el-upload>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col style="margin-left:1%;width: 45%" >
                    <div class="grid-content">
                        <el-form-item label="()" prop="avator" label-width="180px">
                            <el-upload
                                    class="avatar-uploader"
                                    action="http://124.222.128.245:955/uploadImageFile"
                                    :show-file-list="false"
                                    accept="image/jpeg,image/gif,image/png,image/jpg"
                                    :on-success="handleAvatarSuccess_Li">
                                <img v-if="doctorsLicense" :src="doctorsLicense" class="avatar" style="width: 100px;height: 100px">
                                <i v-else class="el-icon-plus avatar-uploader-icon" slot="trigger"></i>
                            </el-upload>
                        </el-form-item>
                    </div>
                </el-col>
            </el-row>
            <el-row style="width: 100%">
                <el-col :span="12" :offset="6">
                    <el-form-item>
                        <el-button type="primary" @click="register('form')"></el-button>
                        <el-button @click="login"></el-button>
                    </el-form-item>
                </el-col>
            </el-row>


        </el-form>
    </div>
</template>
<script>
    import md5 from 'js-md5'
    export default {
        mounted(){
        },
        data() {

            return {
                content:'',
                choice:'',
                avatar:'',
                doctorsLicense:'',
                picUrl:'',
                runNum:60,
                provinceCode:'',
                cityCode:'',
                countryCode:'',
                display:1,
                form: {
                    doctorAvatar:'',
                    doctorName: '',
                    doctorPhone: '13044124565',
                    verification: '4586',
                    doctorSex:'1',
                    doctorCertificate:'',
                    doctorPassword:'1999319xjj',
                    doctorIdCard:'362202199903193316',
                    doctorSchool: '',
                    doctorAddress:'',
                },
                password2:'1999319xjj',
                province:[],
                city:[],
                county:[],
                area: {11:"",12:"",13:"",14:"",15:"",21:"",22:"",23:"",
                    31:"",32:"",33:"",34:"",35:"",36:"",37:"",41:"",42:"",
                    43:"",44:"",45:"",46:"",50:"",51:"",52:"",53:"",54:"",
                    61:"",62:"",63:"",64:"",65:"",71:"",81:"",82:"",91:""
                },
                Wi: [7,9,10,5,8,4,2,1,6,3,7,9,10,5,8,4,2 ],
                ValideCode: [1,0,'X',9,8,7,6,5,4,3,2],
                value: ''
            };
        },
        methods: {

            handleAvatarSuccess_avatar(res, file) {
                this.avatar = URL.createObjectURL(file.raw);
                this.form.doctorAvatar = res.data;
            },
            handleAvatarSuccess_Li(res, file) {
                this.doctorsLicense = URL.createObjectURL(file.raw);
                this.form.doctorCertificate = res.data;
            },
            login(){

            },

            register(){
                if(this.form.doctorName != '' && this.form.doctorSex != '' && this.form.doctorAvatar != ''
                && this.form.doctorCertificate != '' && this.form.doctorIdCard != '') {
                    if (this.form.doctorPassword != this.password2) {
                        this.$message.error("2")
                    }else if (!this.checkCard()){
                        this.$message.error("1670")
                    }
                    let url = "/api/message/provide/doctor/addDoctor";
                    this.form.doctorPassword = this.$md5(this.form.doctorPassword);
                    console.log(this.form)
                    this.$axios.post(url, this.form).then((res)=>{
                        if (res.data.code == 200) {
                            this.$message.success(res.data.message)
                            this.sleep(500);
                            // this.$router.push({
                            //     name:"login"
                            // })
                        } else {
                            this.form.doctorPassword = this.password2;
                            this.$message.error(res.data.message)
                        }

                    })
                } else {
                    this.$message.error("")
                }
            },

            getVerification(){
                console.log(this.form.doctorPhone)
                if (this.form.doctorPhone != '') {
                    if (!this.checkPhone()) {
                        this.$message.error("")
                    } else {
                        this.$axios.get("/api/message/provide/verification/send/"+this.form.doctorPhone).then((res)=>{
                            this.$message.success(res.data.message)
                        })
                        this.display=2;
                        let that = this;
                        let myTime = setInterval(function () {
                            that.runNum--;
                            if(that.runNum <= 0){
                                that.content = ""
                                that.display = 1;
                                that.runNum = 60;
                                clearInterval(myTime);
                            }else {
                                that.content = that.runNum+"s";
                            }
                        },1000);
                        this.content="60s"
                    }

                } else {
                    this.$message.error("")
                }


            },
            checkPhone(){
                let re = /^[1][3,4,5,7,8][0-9]{9}$/;
                let str = this.form.doctorPhone;
                if(re.test(str)){
                    return true;

                }else {
                    return false;
                }
            },

            checkCard() {
                if(!this.form.doctorIdCard) return;
                let CardId = this.form.doctorIdCard;
                if(CardId.length == 15) {
                    if(this.is15Card(CardId)) {
                        this.go(CardId.length);
                    }
                    else {
                        return this.$message({type: 'error', message: ''});
                    }
                } else if (CardId.length == 18) {
                    let a_iden = CardId.split("");
                    if (this.is18Card(CardId) && this.is18CardEnd(a_iden) ) {
                        this.go(CardId.length);
                        return this.is18Card(CardId);
                    }
                    else {
                        return this.$message({type: 'error', message: ''});
                    }
                } else {
                    return this.$message({type: 'error', message: ''});
                }
            },

            // 18
            is18CardEnd(a_idCard) {
                let sum = 0;
                if (a_idCard[17].toLowerCase() == 'x') {
                    a_idCard[17] = 10;
                }
                for ( var i = 0; i < 17; i++) {
                    sum += this.Wi[i] * a_idCard[i];
                }
                let valCodePosition = sum % 11;
                if (a_idCard[17] == this.ValideCode[valCodePosition]) {
                    return true;
                } else {
                    return false;
                }
            },
            // 
            is18Card(idCard18){
                let year =  idCard18.substring(6,10);
                let month = idCard18.substring(10,12);
                let day = idCard18.substring(12,14);
                let temp_date = new Date(year,parseFloat(month)-1,parseFloat(day));
                if(temp_date.getFullYear()!=parseFloat(year)
                    ||temp_date.getMonth()!=parseFloat(month)-1
                    ||temp_date.getDate()!=parseFloat(day)){
                    return false;
                }else{
                    return true;
                }
            },
            is15Card(idCard15){
                let year =  idCard15.substring(6,8);
                let month = idCard15.substring(8,10);
                let day = idCard15.substring(10,12);
                let temp_date = new Date(year,parseFloat(month)-1,parseFloat(day));
                if(temp_date.getYear()!=parseFloat(year)||temp_date.getMonth()!=parseFloat(month)-1 ||temp_date.getDate()!=parseFloat(day)) {
                    return false;
                }else{
                    return true;
                }
            },
            sleep(numberMillis) {
                var now = new Date();
                var exitTime = now.getTime() + numberMillis;
                while (true) {
                    now = new Date();
                    if (now.getTime() > exitTime)
                        return;
                }
            },

            // 
            go(val) {
                let iden = this.form.doctorIdCard;
                let sex = null;
                let birth = null;
                let myDate = new Date();
                let month = myDate.getMonth() + 1;
                let day = myDate.getDate();
                let age = 0;
                if(val===18){
                    age = myDate.getFullYear() - iden.substring(6, 10) - 1;
                    sex = iden.substring(16,17);
                    birth = iden.substring(6,10)+"-"+iden.substring(10,12)+"-"+iden.substring(12,14);
                    if (iden.substring(10, 12) < month || iden.substring(10, 12) == month && iden.substring(12, 14) <= day)
                        age++;
                    if(age>=16 && age <= 70){
                        return true;
                    }else{
                        return false;
                    }

                }
                if(val===15){
                    age = myDate.getFullYear() - iden.substring(6, 8) - 1901;
                    sex = iden.substring(13,14);
                    birth = "19"+iden.substring(6,8)+"-"+iden.substring(8,10)+"-"+iden.substring(10,12);
                    if (iden.substring(8, 10) < month || iden.substring(8, 10) == month && iden.substring(10, 12) <= day)
                        age++;
                    if(age>=18){
                        return true;
                        this.$message.success("")
                    }else{
                        return false;
                        this.$message.error("")
                    }
                }

            }

        }
    }
</script>
<style scoped>
    .el-row{
        margin-bottom: 10px;
    }
    .el-col {
        border-radius: 2px;
    }
    .grid-content {
        border-radius: 10px;
        min-height: 30px;
    }
    .but {
        width: 25%;
        height: 40px;
        margin-left: 5px;
    }
</style>
