<template>
  <div class="page-container">
    <div class="title" >
      <span>(2025-08-23)</span>
    </div>
    <div v-if="countdown > 0" class="content">
      <div>
        <el-tag hit="true" color="#f0f0f0" style="font-size: 28px; color: red;" disable-transitions="true">
          <span>{{ days }}</span>
        </el-tag>
        <el-tag hit="true" color="#f0f0f0" style="font-size: 28px;color: red;">
          <span>{{ hours }}</span>
        </el-tag>
        <el-tag hit="true" color="#f0f0f0" style="font-size: 28px;color: red;">
          <span>{{ minutes }}</span>
        </el-tag>
        <el-tag hit="true" color="#f0f0f0" style="font-size: 28px;color: red;">
          <span>{{ seconds }}</span>
        </el-tag>
      </div>
      <div>
        <div>
          <a href="https://cpaexam.cicpa.org.cn/" class="cpa-link"></a>
        </div>
        <div>
          <a href="https://cpaexam.cicpa.org.cn/files/articlehtml/statichtml/01/0000018BFF19B498C0B03256BD2BB543.html" class="cpa-link"></a>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  data() {
    return {
      countdown: this.calculateCountdown(),
      interval: null
    };
  },
  computed: {
    days() {
      return Math.floor(this.countdown / (60 * 60 * 24));
    },
    hours() {
      return Math.floor((this.countdown % (60 * 60 * 24)) / (60 * 60));
    },
    minutes() {
      return Math.floor((this.countdown % (60 * 60)) / 60);
    },
    seconds() {
      return Math.floor(this.countdown % 60);
    }
  },
  methods: {
    calculateCountdown() {
      const newYear = new Date('August 23, 2025 00:00:00').getTime();
      const now = new Date();
      return Math.max((newYear - now) / 1000, 0);
    },
    startCountdown() {
      this.interval = setInterval(() => {
        this.countdown -= 1;
        if (this.countdown <= 0) {
          clearInterval(this.interval);
        }
      }, 1000);
    }
  },
  mounted() {
    this.startCountdown();
  },
  beforeDestroy() {
    clearInterval(this.interval);
  }
};
</script>

<style scoped>
.page-container {
  background-color: #f0f0f0; /*  */
  display: flex;
  flex-direction: column; /*  */
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.title {
      flex: 2;
      margin: 50px;
      text-align: center;
      font-size: 2em;
}
.content {
  flex: 3;
  margin: 5px;
  text-align: center;
}
.countdown-values {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}
.el-tag {
  background-color: #409eff;
  color: #fff;
  padding: 5px 10px;
}
.links-container {
  margin-top: 20px;
}
.cpa-link {
  color: #007bff;
  text-decoration: none;
  transition: color 0.3s;
  margin: 20dp;
}
.cpa-link:hover {
  color: #0056b3;
}
</style>
