<template>
    <div class="cantainer">
        <div class="handle-box">
            <el-input class="search-input" placeholder="" size="small" v-model="ListSearch"></el-input>
            <el-button type="primary" @click="search" class="handle-del mr10" style="margin-left: 5px;height: 35px;margin-top: 8px"></el-button>
        </div>
        <el-table
                :data="list"
                tooltip-effect="dark"
                style="width: 100%"
        >
            <!--<el-table-column type="selection" width="55"></el-table-column>-->
            <el-table-column
                    prop="messageType"
                    label=""
                    width="100">
                <template slot-scope="scope">
                    {{scope.row.messageType | getType(scope.row.messageType)}}
                </template>
            </el-table-column>

            <el-table-column
                    prop="messageCreateTime"
                    label=""
                    width="200"
                    >
            </el-table-column>
            <el-table-column
                    prop="messageContent"
                    label=""
                    show-overflow-tooltip>
                <!--<template slot-scope="scope">-->
                    <!--&lt;!&ndash;{{scope.row.doctorSex | getSex(scope.row.doctorSex)}}&ndash;&gt;-->
                <!--</template>-->
            </el-table-column>
            <el-table-column
                    prop="messageStatus"
                    label=""
                    sortable
                    width="120">
                <template slot-scope="scope">
                {{scope.row.messageStatus | getStatus(scope.row.messageStatus)}}
                </template>
            </el-table-column>


            <el-table-column label="" width="200">
                <template slot-scope="scope">
                    <el-button
                        size="mini"
                        @click="read(scope.row)"
                        type="message"
                    ></el-button>
                    <el-button
                        size="mini"
                        @click="del(scope.row)"
                        type="danger"
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 20, 40]"
                :page-size="pageSize"
                layout="total,prev, pager, next, jumper"
                :total="totalNum">
        </el-pagination>
    </div>
</template>

<script>
    export default {
        name: "sys-message",
        data() {
            return {
                list:[], //
                totalNum:0,
                dialogFormVisible:false,   //
                ListSearch:"",  //
                editFormVisible: false,  //
                ruleForm:{},
                doctorCertificates:[],
                addLoading:false,
                currentPage:1, //
                pageSize:5 , //	
                content:'',
                user:{},
            }
        },
        mounted(){
            this.user = JSON.parse(localStorage.getItem("user"));
            if (this.user == undefined) {
                this.$router.push("/login")
            }
            this.getAllMessage()
        },
        filters: {
            getType: (msg) => { // msga
                if (msg == 5) {
                    return ""
                } else {
                    return ""
                }
            },
            getBelong:(name,msg)=>{
                console.log("msg:"+msg)
                console.log("name:"+name)
                if (msg == 3) {
                    return ""
                } else{
                    return name;
                }
            },
            getStatus: (msg) =>{
                if (msg == 0) {
                    return ""
                } else  {
                    return ""
                }
            }
        },
        methods: {
            read(row){
                let data = {
                    'messageStatus': '1',
                    'messageId': row.messageId,
                }
                this.$axios.post("/api/message/provide/message/updateMessage",data).then((res)=>{

                    this.$message.success("")
                    this.getAllMessage();
                })
            },
            del(row){
                let data = {
                    'messageStatus': '2',
                    'messageId': row.messageId,
                }
                this.$axios.post("/api/message/provide/message/updateMessage",data).then((res)=>{
                    this.$message.success("")
                    this.getAllMessage();
                })
            },
            search() {
                this.content = this.ListSearch
                this.getAllMessage();
                this.pageNum = 1;
            },
            // currentPagepagesizedata
            handleSizeChange: function (size) {
                this.pageSize = size;
                this.getAllMessage();
            },
            handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                this.getAllMessage();
            },
            getAllMessage(){
                let data = {
                    'pageSize': this.pageSize,
                    'pageNum': this.currentPage,
                    'messageType': '3,5',
                    'messageGet': this.user.userId,
                    'content': this.content,
                }
                this.$axios.post("/api/message/provide/message/getAMessageBuyType",data).then((res)=>{
                    this.list = res.data.data.records
                    this.currentPage = res.data.data.current
                    this.totalNum = res.data.data.total;
                    console.log(res.data.data)
                    this.$message.success(res.data.message)
                })
            }
        }
    }
</script>

<style scoped>
    .cantainer {
        padding: 10px;
        background: #ffffff;
        border-radius: 5px;
        border: 1px #ddd solid;
        margin-top: 20px;
    }
    .search-input {
        width: 200px;
        margin-left: 50px;
    }
    .el-table__body-wrapper{
        height: 500px;
    }
    .crumbs{
        margin-bottom:20px;
    }
</style>